body {
  background: #FFF;
  margin: 0px;
}

h1, h2, h3, h4, p {
  margin: 0px;
}

* {
  box-sizing: border-box;
}

@font-face {
  font-family: "LucidaStd";
  src: url("../fonts/LucidaStd.eot");
  src: url("../fonts/LucidaStd.eot?#iefix") format("embedded-opentype"), url("../fonts/LucidaStd.woff2") format("woff2"), url("../fonts/LucidaStd.woff") format("woff"), url("../fonts/LucidaStd.ttf") format("truetype");
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: 'LucidaStd-Bold';
  src: url("../fonts/LucidaStd-Bold.eot");
  src: url("../fonts/LucidaStd-Bold.eot?#iefix") format("embedded-opentype"), url("../fonts/LucidaStd-Bold.woff2") format("woff2"), url("../fonts/LucidaStd-Bold.woff") format("woff"), url("../fonts/LucidaStd-Bold.ttf") format("truetype");
  font-weight: 300;
  font-style: normal;
  font-family: 'icomoon';
  src: url("../fonts/icomoon.eot?ecg8pc");
  src: url("../fonts/icomoon.eot?ecg8pc#iefix") format("embedded-opentype"), url("../fonts/icomoon.ttf?ecg8pc") format("truetype"), url("../fonts/icomoon.woff?ecg8pc") format("woff"), url("../fonts/icomoon.svg?ecg8pc#icomoon") format("svg");
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font endering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.chevron-right:before {
  font-family: 'icomoon';
  content: "\e900";
  font-style: normal;
}

.comment__bubble:before {
  font-family: 'icomoon';
  content: "\e96e";
  font-style: normal;
  padding-right: 5px;
  font-size: 13px;
  vertical-align: -2px;
}

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0;
}

.clearfix {
  display: inline-block;
}

/* start commented backslash hack \*/
* html .clearfix {
  height: 1%;
}

.clearfix {
  display: block;
}

/* close commented backslash hack */
.page {
  font-family: 'Barlow Condensed', sans-serif;
  padding-top: 33px;
  background: #c4dae6;
  background-size: contain;
  background-repeat: no-repeat;
}

.page-content {
  max-width: 1000px;
  margin: 0 auto;
}

.header__top {
  min-height: 40px;
  padding: 10px 0px 0px 32px;
  background: linear-gradient(top, #ffffff, #e2e2e2);
  border-radius: 3px 3px 0 0;
}

.header__button {
  width: 46px;
  height: 18px;
  line-height: 18px;
  color: #fff;
  text-transform: uppercase;
  font-size: 14px;
  text-decoration: none;
  background-color: #85afbd;
  display: inline-block;
  text-align: center;
  border-radius: 2px;
}

.header__button:hover {
  color: #989898;
  background-color: #87d5de;
}

.header__news {
  font-size: 12px;
  font-family: "Georgia";
  color: #989898;
  line-height: 1.5;
  text-decoration: none;
  font-style: italic;
  margin-left: 13px;
}

.header__news:hover {
  color: #87d5de;
}

.header__wrap {
  background-color: #092f3b;
  min-height: 126px;
  padding: 25px 0px 0px 32px;
  background-size: cover;
  background-repeat: no-repeat;
}

.header__logo {
  float: left;
  display: inline-block;
  text-align: center;
}

.header__logo_top {
  font-size: 30px;
  font-family: "LucidaStd-Bold";
  color: #f6f6f6;
  line-height: 0.754;
  text-decoration: none;
  text-transform: uppercase;
}

.header__logo_center {
  font-size: 22px;
  font-family: "Barlow Condensed";
  color: #f6f6f6;
  line-height: 1.222;
  text-transform: uppercase;
  font-weight: 300;
  letter-spacing: 9px;
}

.header__logo_bottom {
  font-size: 10px;
  font-family: "LucidaStd, sans-serif";
  color: #6892a0;
  line-height: 2.2;
  margin-top: 8px;
  padding-top: 8px;
  text-transform: uppercase;
  letter-spacing: 1px;
  display: block;
  border-top: 1px solid;
  color: #6892a0;
  text-align: center;
}

.header__nav {
  float: left;
  padding: 0px 0px 0px 26px;
}

.header__nav ul {
  list-style: none;
  overflow: hidden;
  padding: 0;
  margin: 0;
}

.header__nav li {
  float: left;
}

.header__nav li a {
  display: block;
  text-decoration: none;
  font-size: 18px;
  font-family: "Barlow Condensed";
  color: #fff;
  line-height: 1.222;
  text-transform: uppercase;
  padding: 0px 14px;
}

.nav li a {
  padding: 5px 14px;
}

.nav li a:hover {
  background: #092f3b;
  box-shadow: inset 1px 1px 1px 0px rgba(0, 0, 0, 0.28);
  box-shadow: 1px 1px 0px 0px rgba(255, 255, 255, 0.1);
  border-radius: 3px;
}

.nav {
  padding-top: 10px;
}

.header__link ul li a {
  font-size: 14px;
  font-family: "Barlow Condensed";
  color: darkgray;
  line-height: inherit;
  text-transform: uppercase;
  font-weight: 300;
}

.header__link ul li a:hover {
  color: #87d5de;
}

.content {
  background: #ededed;
  padding: 30px 15px 0px 15px;
  min-height: 1126px;
}

.main {
  width: 77.7%;
  float: left;
  min-height: 500px;
}

.aside {
  width: 22.3%;
  float: left;
  min-height: 500px;
}

.top-news {
  padding: 0 15px;
}

.title-block {
  min-height: 35px;
  background: #cacaca;
  border-radius: 3px;
  margin-bottom: 5px;
}

.title-block__title {
  background: #ae0002;
  min-height: 36px;
  border-radius: 3px 0 0 3px;
  display: inline-block;
  padding: 0px 35px 0px 12px;
  position: relative;
}

.title-block__title:after {
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  left: 100%;
  top: 0px;
  border: 18px solid;
  border-color: transparent transparent transparent #ae0002;
}

.title-block__title a {
  font-size: 18px;
  font-family: "Barlow Condensed";
  color: #fff;
  line-height: 36px;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 300;
}

.title-block__title a:hover {
  color: #87d5de;
}

.title-block__caption {
  font-size: 12px;
  font-family: "Georgia";
  color: #989898;
  line-height: 1.5;
  text-align: justify;
  padding-left: 40px;
  vertical-align: 2px;
}

.background-img {
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}

.slider__slide {
  min-height: 256px;
  position: relative;
}

.slider__content {
  position: absolute;
  background: #000;
  left: 37px;
  bottom: 36px;
  max-width: 336px;
  padding: 17px;
}

.slider__caption a {
  font-size: 12px;
  font-family: "Georgia";
  color: #fff;
  line-height: 1.5;
  text-decoration: none;
}

.slider__caption a:hover {
  color: #87d5de;
}

.slick-dots {
  bottom: 10px;
  color: white;
}

.slick-dots li.slick-active button:before {
  color: white;
  opacity: 1;
}

.slick-dots li button:before {
  opacity: .50;
  color: white;
}

.slick-dotted.slick-slider {
  margin-bottom: 5px;
}

.main-news {
  padding: 0 7px 6px 7px;
}

.news-col {
  float: left;
  width: 25%;
  padding: 8px;
}

.news-col__content {
  background: #fff;
  min-height: 281px;
  padding: 20px 14px 0px 14px;
  font-size: 11px;
  font-family: "Georgia";
  color: #989898;
  line-height: 1.636;
  font-style: italic;
  box-shadow: 1px 1px 1px 0px rgba(0, 0, 0, 0.14);
}

.news-col__content .comment {
  text-align: right;
}

.comment__link {
  text-decoration: none;
  color: #989898;
}

.comment__link:hover {
  color: #87d5de;
}

.news-col__img {
  height: 116px;
  width: 100%;
  object-fit: cover;
}

.news-col__title {
  padding: 10px 0px 5px 0px;
}

.news-col__title a {
  font-size: 11px;
  font-family: "Georgia";
  color: #2e2e2e;
  line-height: 1.5;
  font-style: normal;
}

.news-col__title a:hover {
  color: #87d5de;
}

.other-news {
  padding: 0 15px 50px 15px;
}

.post-wrap {
  margin-top: 30px;
}

.post {
  margin-top: 25px;
}

.post__left-col {
  float: left;
  width: 23.5%;
}

.post__right-col {
  float: left;
  width: 76.5%;
}

.post__img {
  height: 130px;
  width: 100%;
  object-fit: cover;
}

.post__content {
  padding: 10px 0px 0px 30px;
  min-height: 100px;
}

.post__text {
  font-size: 12px;
  font-family: "Georgia";
  color: #989898;
  line-height: 1.5;
  padding-bottom: 15px;
}

.tags, .social {
  display: inline-block;
}

.post__info {
  font-size: 12px;
  font-family: "Georgia";
  color: #989898;
  line-height: 1.5;
}

.post__info .comment {
  padding-right: 18px;
  display: inline-block;
}

.post__info .tags__tag {
  display: inline-block;
  font-style: italic;
  color: #2b5e5e;
}

.post__info .tags__tag:hover {
  color: #87d5de;
}

.social__icon {
  display: inline-block;
  text-decoration: none;
  padding: 5px 2px;
}

.social__icon img {
  vertical-align: -3px;
}

.social__icon img:hover {
  box-shadow: 0px 0px 0px 2px #87d5de;
  border-radius: 3px;
}

.tags {
  padding: 5px 22px 5px 5px;
}

.tags__caption, .social__share {
  padding-right: 3px;
}

.hottest-video {
  padding: 0 14px;
}

.hottest-video_col {
  min-height: 500px;
}

.hottest-video__title {
  font-size: 18px;
  font-family: "Barlow Condensed";
  color: #fff;
  line-height: 36px;
  background: #ae0002;
  height: 36px;
  font-weight: 300;
  text-transform: uppercase;
  padding-left: 15px;
  border-radius: 3px;
  margin-bottom: 5px;
}

.video-col__content {
  min-height: 292px;
  padding-bottom: 30px;
  font-size: 11px;
  font-family: "Georgia";
  color: #989898;
  line-height: 1.636;
  font-style: italic;
}

.video-col__content .comment {
  display: inline-block;
  padding-left: 10px;
}

.video-col__img {
  height: 172px;
  width: 100%;
  position: relative;
}

.video-col__title {
  padding: 15px 0px 5px 0px;
}

.video-col__title a {
  font-size: 11px;
  font-family: "Georgia";
  color: #2e2e2e;
  line-height: 1.5;
  font-style: normal;
}

.video-col__title a:hover {
  color: #87d5de;
}

.video-col__icon {
  height: 36px;
  width: 36px;
  background: #000;
  position: absolute;
  left: 11px;
  bottom: 11px;
  background-position: 60% center;
  background-repeat: no-repeat;
  border-radius: 3px;
}

.footer {
  margin-top: 277px;
  min-height: 174px;
}

.footer__copy {
  font-size: 19px;
  font-family: "LucidaStd";
  color: #073c58;
  line-height: 0.754;
  text-transform: uppercase;
  padding-left: 5px;
}

@media (max-width: 903px) {
  .main, .aside {
    width: 100%;
    float: none;
  }
  .header__nav {
    padding: 0px 0px 20px 0px;
    float: none;
  }
  .header__logo {
    float: none;
    margin-bottom: 10px;
  }
  .header__wrap {
    text-align: center;
  }
}

@media (max-width: 766px) {
  .title-block__title:after {
    display: none;
  }
  .title-block__title {
    display: block;
  }
  .title-block__caption {
    padding-left: 12px;
  }
}

@media (max-width: 580px) {
  .news-col {
    width: 50%;
  }
  .header__top, .header__wrap {
    padding-left: 10px;
  }
}

@media (max-width: 500px) {
  .title-block__title a {
    font-size: 14px;
    padding-right: 0px;
  }
  .slider__content {
    left: 10px;
  }
  .post__left-col, .post__right-col {
    width: 100%;
    float: none;
  }
  .post__content {
    padding-left: 0px;
  }
}

@media (max-width: 350px) {
  .news-col {
    width: 100%;
  }
}
